.la-ball-clip-rotate-pulse {
  position: relative;
  box-sizing: border-box;
  > div {
    position: relative;
    box-sizing: border-box; }
  display: block;
  font-size: 0;
  color: #fff;

  transform: color 5s;

  &.la-dark {
    color: #000; }
  .hover & {
    color: #000; }
  > div {
    display: inline-block;
    float: none;
    background-color: currentColor;
    border: 0 solid currentColor;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 100%;
    &:first-child {
      position: absolute;
      width: 32px;
      height: 32px;
      background: transparent;
      border-style: solid;
      border-width: 2px;
      border-right-color: transparent;
      border-left-color: transparent;
      animation: ball-clip-rotate-pulse-rotate 1s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite; }
    &:last-child {
      width: 16px;
      height: 16px;
      animation: ball-clip-rotate-pulse-scale 1s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite; } }
  width: 32px;
  height: 32px;
  &.la-sm {
    width: 16px;
    height: 16px;
    > div {
      &:first-child {
        width: 16px;
        height: 16px;
        border-width: 1px; }
      &:last-child {
        width: 8px;
        height: 8px; } } }
  &.la-2x {
    width: 48px;
    height: 48px;
    > div {
      &:first-child {
        width: 48px;
        height: 48px;
        border-width: 2px; }
      &:last-child {
        width: 24px;
        height: 24px; } } }
  &.la-3x {
    width: 96px;
    height: 96px;
    > div {
      &:first-child {
        width: 96px;
        height: 96px;
        border-width: 6px; }
      &:last-child {
        width: 48px;
        height: 48px; } } } }

/*
 * Animations */
@keyframes ball-clip-rotate-pulse-rotate {
  0% {
    transform: translate(-50%, -50%) rotate(0deg); }
  50% {
    transform: translate(-50%, -50%) rotate(180deg); }
  100% {
    transform: translate(-50%, -50%) rotate(360deg); } }



@keyframes ball-clip-rotate-pulse-scale {
  0%,
  100% {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1); }
  30% {
    opacity: .3;
    transform: translate(-50%, -50%) scale(0.15); } }
