@charset "UTF-8";


html.turbolinks-progress-bar::before {
  background-color: black;
  height: 4px !important; }


//////////////////////////////////////////////////////////////////////////////////////
// show/hide elements on home screen
//////////////////////////////////////////////////////////////////////////////////////

.cms-debug-bar {
  height: 0 !important; }

body {
  #onair_container,
  #achat_container {
    display: none; } }
body.cms-home {
  #onair_container,
  #achat_container {
    display: block; } }



//////////////////////////////////////////////////////////////////////////////////////
// global styles
// only for generic styling! specific/detailed styles in own files please!
//////////////////////////////////////////////////////////////////////////////////////
body, html {
  height: 100%;
  letter-spacing: 1px;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }


