
body.cms-base,
body.app-base {
  .content-container {
    padding-top: 100px;
    padding-bottom: 100px; } }

body.cms-home {
  .content-container {
    display: none; } }

.cmsplugin_holder {
  font-weight: 300; }

.main-content > .textplugin_holder {

  font-weight: 300;
  padding: 4px 0 16px;
  border-top-width: 2px;
  border-top-style: solid;

  h1, h2 {
    &:first-child {
      padding: 12px 0 8px; } }

  p {
    text-indent: 30px;
    text-indent: 14px;
    img {
      margin-left: -30px;
      margin-left: -14px; } }

  a {
    //text-decoration: underline
    border-bottom: 1px dotted black;
    &:hover {
      border-bottom: 1px solid black; } } }






///////////////////////////////////////////////////////////////////////
// filer plugins
///////////////////////////////////////////////////////////////////////
.filer-image {
  figcaption {
    opacity: 0.7;
    font-size: 90%; }

  img.left {
    float: left; }

  &.inline img {
    display: inline;
    padding-top: 6px;
    padding-right: 12px;
    padding-bottom: 2px; } }
